import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state:{ //
		hasLogin : false ,//
		userInfo : {
			uid:'',
			openid:''
		} ,//
	},
	mutations:{
		login(state,provider){
			console.log("");
			console.log(provider);
			console.log(state);
			state.hasLogin = true ;
			state.userInfo = provider ;
			uni.setStorage({
				key:'userInfo',
				data: provider
			});
		},
		logout(state){
			console.log("");
			state.hasLogin = false ;
			state.userInfo = {} ;
			uni.removeStorage({
				key : 'userInfo'
			});
		}
	} 
})

export default store 