const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://xx.db2j.com/index.php',
		loadingText: '~',
		loadingTime: 800,
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
		// ......
	});
	
	// 
	Vue.prototype.$getimgurl = function(imgstr){
		return imgstr.replace(new RegExp('https://xx.db2j.com', 'g'), '')
	}
	
}

export default {
	install
}